@import nulstyle


html
	scroll-behavior: smooth
body
	background: #1C1B21
	color: #FFFFFF
	font-family: 'Poppins', sans-serif
a
	color: #FFFFFF
.container
	max-width: 1140px
	width: 100%
	margin: 0 auto
	padding: 0 15px
.header
	background-image: url(../images/background.jpg)
	background-size: cover
	background-position: center
	background-repeat: no-repeat
.header__line
	display: flex
	justify-content: center
	align-items: center
	padding: 30px 0 322px 0
.header__list
	display: flex
	justify-content: center
	align-items: center
.logo-h
	padding-right: 65px
.ol
	padding-right: 56px
.ol:nth-child(6)
	padding: 0
.header__link
	font-weight: 600
	font-size: 18px
	line-height: 27px
.header__text
	font-weight: 700
	font-size: 48px
	line-height: 72px
.header__title
	font-weight: 700
	font-size: 100px
	line-height: 150px
	padding-bottom: 300px
@supports (-webkit-text-stroke: 1px white)
	.header__title
		text-shadow: none
		color: transparent
		-webkit-text-stroke: 1px #fff
.sub-header__images
	display: flex
	justify-content: space-between
	margin-top: -150px
.show
	opacity: 0
	background: #7A66CC
	border-radius: 20px 0px
	margin-top: -88px
	transition: .2s
.sub-header__text
	padding: 20px
	font-weight: 500
	font-size: 16px
	line-height: 24px
.sub-header__image:hover
	.show
		opacity: 0.7
.sub-header
	padding-bottom: 179px
.sub-header__img
	max-width: 350px
	width: 100%
	padding-right: 15px
.sub-header__img:nth-child(3)
	padding: 0


/*---------------------------------*/


.Bright
	padding-bottom: 151px
.Bright-logo
	display: flex
	align-items: center
.logo2
	padding-right: 30px
	max-height: 50px
.title
	font-weight: 700
	font-size: 48px
	line-height: 72px
	color: #FFFFFF
.Bright-line
	display: flex
	justify-content: space-between
	align-items: center
.Bright-line__column
	max-width: 440px
	width: 100%
.Bright__text
	font-weight: 400
	font-size: 16px
	line-height: 24px
	opacity: 0.7
.br
	padding-bottom: 35px
.Bright-line__column2
	position: relative
	width: 445px
	height: 466px
.girl1
	z-index: 10
	position: absolute
.girl2
	z-index: 5
	position: absolute
	right: 0
	bottom: 0
.label__columns
	padding-top: 60px
	display: flex
	justify-content: space-between
	max-width: 580px
	margin-top: -80px
.label__column
	flex-direction: column
.label__column-tap
	display: flex
	align-items: center
.label__text
	font-weight: 600
	font-size: 18px
	line-height: 27px
	padding-left: 20px
.label__column-tap
	padding: 0 40px 30px 0
.Lights__body
	background: #1C1B21
	border: 1px solid #FFFFFF
	border-radius: 30px 0px
	padding: 50px 80px
	position: relative
.Lights__text 
	font-weight: 400
	font-size: 18px
	line-height: 27px
	max-width: 500px
.line
	width: 520px
	height: 0px
	opacity: 0.6
	border: 1px solid #FFFFFF
	margin: 25px 0
.Lights__link
	font-weight: 700
	font-size: 18px
	line-height: 27px
	color: #7A66CC
.Lights__img
	position: absolute
	right: 80px
	top: -100px
.Lights
	padding-bottom: 180px


/*-=-=-=-=-=-==-=--==-=-=-=-=-=-==-=--==--==-=*/


.track__columns
	position: relative
.track__column
	padding-top: 50px
.track__row
	padding-bottom: 20px
.track__row2
	display: flex
	align-items: center
.track__text
	font-weight: 700
	font-size: 14px
	line-height: 21px
	opacity: 0.5
	padding-right: 20px
.track__link
	font-weight: 400
	font-size: 18px
	line-height: 27px
	cursor: pointer
	transition: all .1s
	background: #1C1B21
	color: #fff
.track__link.active
	color: #7A66CC
.img-pl.active
	display: block
.track__play.active
	display: block

.track__play
	padding: 20px 0 50px 0
	display: none
.track__columns
	display: flex
	align-items: center
	padding-top: 40px
	justify-content: space-between
.img-pl
	width: 350px
	height: 350px
	border-radius: 250px
	position: absolute
	top: 20px
	right: 20px
.track__img-box1
	width: 430px
	height: 430px
	border-radius: 250px
	position: absolute
	background-color: #2C2C2C
	top: 60px
	right: 0
.track__img-box2
	width: 390px
	height: 390px
	border-radius: 250px
	position: absolute
	background-color: #7A66CC
	top: 20px
	right: 20px


/*-=-=-==-=-=-=-=-=*/

.galery
	padding: 180px 0
.grid
	padding-left: 40px
	display: grid
	grid-template-columns: repeat(4, 255px)
	grid-template-rows: minmax(200px, 235px)
	grid-gap: 30px
.galery-img:nth-child(5)
	grid-column: 2 / 4
	opacity: 1
.galery-img
	max-width: 540px
	width: 100%
	transition: all .3s
	&:hover
		opacity: 0.3


/*-=-==-=-=-=-=-=-=-=--=*/


.slider
	padding-bottom: 180px
.container
	position: relative
.slick-track
	display: flex
.slick-list
	overflow: hidden
.slider__box
	background-color: #2F2B2B
	flex-direction: column
.slider__title
	font-weight: 700
	font-size: 18px
	line-height: 27px
	opacity: 0.5
	padding: 30px 0 20px 35px
.slider__sub-title
	font-weight: 500
	font-size: 18px
	line-height: 27px
	padding: 0 0 30px 35px
.slick-slide
	margin-right: 20px
	border-radius: 30px 0px
.slider__buttons
	display: flex
	justify-content: space-between
.slider__button
	font-weight: 700
	font-size: 16px
	line-height: 24px
	text-transform: uppercase
	color: #FFFFFF
	padding: 18px 52px
	background: #7A66CC
	border-radius: 30px 0px
.slick-arrow.slick-prev
	position: absolute
	top: 50%
	left: 0
	margin-left: -50px
	border-radius: 10px 0px
	background: #1C1B21
	border: 2px solid #fff
	padding: 30px 15px
	&::before
		content: ""
		display: block
		margin-left: -7px
		width: 15px
		height: 3px
		background-color: #fff
		transform: rotate(-25deg)
	&::after
		content: ""
		display: block
		margin-left: -7px
		width: 15px
		height: 3px
		background-color: #fff
		transform: rotate(25deg)
.slick-arrow.slick-next
	margin-right: -30px
	position: absolute
	right: 0px
	top: 50%
	border-radius: 10px 0px
	background: #1C1B21
	border: 2px solid #fff
	padding: 30px 15px
	&::before
		content: ""
		display: block
		margin-left: -7px
		width: 15px
		height: 3px
		background-color: #fff
		transform: rotate(25deg)
	&::after
		content: ""
		display: block
		margin-left: -7px
		width: 15px
		height: 3px
		background-color: #fff
		transform: rotate(-25deg)

.slick-arrow.slick-next:hover
	opacity: 0.3
.slick-arrow.slick-prev:hover
	opacity: 0.3

.slick-arrow
	font-size: 0
	width: 10px
	height: 2px

/*-*/


footer
	text-align: center
	padding-bottom: 40px
.footer-title
	font-weight: 400
	font-size: 13px
	line-height: 20px
	text-transform: uppercase
	opacity: 0.5
.footer-logo
	padding-bottom: 60px


.slider-img
	max-width: 350px
	width: 100%
.header__list-box
	display: flex
/*-=-=-=-=--=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/

.burger
	display: none
