		width: 60px
		height: 60px
		border-radius: 50%
		background-color: #7A66CC
	.burger-lines,
	.burger-lines::after,
	.burger-lines::before
		position: absolute
		width: 30px
		height: 4px
		background-color: #fff
	.burger-lines
		top: 28px
		right: 15px
		transform: transition(-50%, -50%)
	.burger-lines::before
		content: ""
		top: -10px
	.burger-lines::after
		content: ""
		top: 10px
	.burger-active .burger-lines
		background-color: transparent
	.burger-active .burger-lines::before
		top: 0
		transform: rotate(45deg)
	.burger-active .burger-lines::after
		top: 0
		transform: rotate(-45deg)
